html,
body,
.root {
  height: 100%;
  background: #f4f2f2;
}

body {
  line-height: 1.5;
}

body,
.app {
  background: #f4f2f2;
}

/* main page */
.list-books-title {
  padding: 10px 0;
  background: -webkit-gradient(linear, left top, left bottom, from(#155b79), to(#527c8e));
  background: -webkit-linear-gradient(#155b79, #527c8e);
  background: -o-linear-gradient(#155b79, #527c8e);
  background: linear-gradient(#155b79, #527c8e);
  text-align: center;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 5;
  -webkit-box-shadow: 0 5px 20px rgba(0,0,0,0.5), 0 1px 16px rgba(0,0,0,0.6);
          box-shadow: 0 5px 20px rgba(0,0,0,0.5), 0 1px 16px rgba(0,0,0,0.6);
}

.list-books-title h1 {
  font-weight: 400;
  margin: 0;
  color: white;
}

.list-books-content {
  padding: 0 0 80px;
  -ms-flex: 1 1;
      flex: 1 1;

}

@media (min-width: 1300px) {
  .list-books-content {
    width: 70%;
    display: block;
    margin: auto;
  }
}

.bookshelf:nth-child(1) h2{
  padding-top: 80px;
}

.bookshelf {
  padding: 0 10px 20px;
}

@media (min-width: 600px) {
  .bookshelf {
    padding: 0 20px 40px;
  }
}

.bookshelf-title {
  border-bottom: 1px solid #dedede;
  font-size: 1.3rem;
}

.bookshelf-books {
  text-align: center;
}

.open-search {
  position: fixed;
  right: 25px;
  bottom: 25px;
}

.open-search a {
  display: block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #155b79;
  background-image: url('./icons/add.svg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: 28px;
  -webkit-box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
          box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  -webkit-transition: -webkit-transform 0.4s ease-in-out, -webkit-box-shadow 0.8s ease-in-out;
  transition: -webkit-transform 0.4s ease-in-out, -webkit-box-shadow 0.8s ease-in-out;
  -o-transition: transform 0.4s ease-in-out, box-shadow 0.8s ease-in-out;
  transition: transform 0.4s ease-in-out, box-shadow 0.8s ease-in-out;
  transition: transform 0.4s ease-in-out, box-shadow 0.8s ease-in-out, -webkit-transform 0.4s ease-in-out, -webkit-box-shadow 0.8s ease-in-out;
  font-size: 0;
}

.open-search a:hover {
  -webkit-transform: scale(1.08);
      -ms-transform: scale(1.08);
          transform: scale(1.08);
  -webkit-box-shadow: 0 1px 10px rgba(0,0,0,0.16), 0 1px 10px rgba(0,0,0,0.23);
          box-shadow: 0 1px 10px rgba(0,0,0,0.16), 0 1px 10px rgba(0,0,0,0.23);
}

/* search page */
.search-books-bar {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 5;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 0 6px rgba(0,0,0,0.23);
          box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 0 6px rgba(0,0,0,0.23);
}

.search-books-input-wrapper {
  -ms-flex: 1 1;
      flex: 1 1;
  background: #f2f0f2;
}

.search-books-bar input {
  width: 100%;
  padding: 15px 10px;
  font-size: 1.25em;
  border: none;
  outline: none;
}

.close-search {
  display: block;
  top: 20px;
  left: 15px;
  width: 50px;
  height: 53px;
  background: white;
  background-image: url('./icons/arrow-back.svg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: 28px;
  font-size: 0;
}

.close-search:hover {
  background-color: rgb(249, 249, 249);
}

.notification {
  padding: 80px;
  visibility: hidden;
  -webkit-animation: 2s fadeIn;
          animation: 2s fadeIn;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 0;
  }
  40% {
    opacity: 0;
  }
  100% {
    visibility: visible;
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 0;
  }
  40% {
    opacity: 0;
  }
  100% {
    visibility: visible;
    opacity: 1;
  }
}

.notification-text {
  padding: 10px;
}

.reset-query-button {
  padding: 5px;
  font-size: 1rem;
}

.reset-query-button:hover {
  background-color: lightgrey;
}

.search-books-results {
  padding: 80px 10px 20px;
}

/* books grid */
.books-grid {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.books-grid li {
  padding: 10px 15px;
  text-align: left;
}

.book {
  width: 140px;
}

.book-title,
.book-authors {
  font-size: 0.7em;
  text-align: center;
}

.book-title {
  margin-top: 10px;
  font-weight: bold;
}

.book-authors {
  color: #999;
}

.book-top {
  position: relative;
  height: 200px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: end;
      align-items: flex-end;
}

.book-shelf-changer {
  position: absolute;
  right: 0;
  bottom: -10px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #a8d2e4;
  background-image: url('./icons/arrow-drop-down.svg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: 20px;
  -webkit-box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
          box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  -webkit-transition: -webkit-transform 0.4s ease-in-out, -webkit-box-shadow 0.8s ease-in-out;
  transition: -webkit-transform 0.4s ease-in-out, -webkit-box-shadow 0.8s ease-in-out;
  -o-transition: transform 0.4s ease-in-out, box-shadow 0.8s ease-in-out;
  transition: transform 0.4s ease-in-out, box-shadow 0.8s ease-in-out;
  transition: transform 0.4s ease-in-out, box-shadow 0.8s ease-in-out, -webkit-transform 0.4s ease-in-out, -webkit-box-shadow 0.8s ease-in-out;
}

.book-shelf-changer:hover {
  -webkit-transform: scale(1.08);
      -ms-transform: scale(1.08);
          transform: scale(1.08);
  -webkit-box-shadow: 0 1px 10px rgba(0,0,0,0.16), 0 1px 10px rgba(0,0,0,0.23);
          box-shadow: 0 1px 10px rgba(0,0,0,0.16), 0 1px 10px rgba(0,0,0,0.23);
}

.book-shelf-changer select {
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}

/* book cover */
.book-cover {
  -webkit-box-shadow: 3px 4px 13px 1px rgba(46,61,73,0.3);
          box-shadow: 3px 4px 13px 1px rgba(46,61,73,0.3);
  background: #eee;
  -webkit-transition: -webkit-box-shadow 0.5s ease-in-out;
  transition: -webkit-box-shadow 0.5s ease-in-out;
  -o-transition: box-shadow 0.5s ease-in-out;
  transition: box-shadow 0.5s ease-in-out;
  transition: box-shadow 0.5s ease-in-out, -webkit-box-shadow 0.5s ease-in-out;
  border-radius: 0 4px 3px 0;
}

.book-cover:hover {
  -webkit-box-shadow: 2px 4px 10px 0 rgba(46,61,73,0.3);
          box-shadow: 2px 4px 10px 0 rgba(46,61,73,0.3);
}

.book-cover-title {
  padding: 20px 10px 0;
  text-align: center;
  font-size: 0.8em;
}


